@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;0,400;0,500;0,800;1,300;1,400&family=Mulish:ital@0;1&display=swap');

body {
    font-family: 'Mulish', sans-serif !important;
}

.game-section {
    h1 {
        font-size: 2em;
        font-weight: 700;
        margin: 30px 0 0 0;
    }

    p {
        font-style: italic;
    }

    .paper-content {
        padding: 15px;
    }

    .aspect-ratio {
        position: relative;
        padding-bottom: 56.25%;
        margin: auto;

        iframe {
            border-radius: 10px;
            position: absolute;
            width: 100%;
            height: 100%;
            left: 0;
            top: 0;
        }
    }

    img {
        &.responsive {
            width: 100%;
        }
    }
}
