@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;0,400;0,500;0,800;1,300;1,400&family=Mulish:ital@0;1&display=swap);
#cover-letter #me{text-align:center}#cover-letter #me img{width:100%;max-width:400px;border-radius:400px;border:10px solid #fff;box-shadow:0 0 10px #aaa;margin:auto}#cover-letter p{text-align:justify}#cover-letter p.lead{font-size:1.3rem;text-align:center;margin-bottom:20px}#cover-letter .session-divider{text-align:center;font-size:40px;color:#aaa;margin-bottom:30px;width:100%}#cover-letter iframe{width:100%}#cover-letter #create-stuff,#cover-letter #gazeus,#cover-letter #solo,#cover-letter #ustwo{display:flex;justify-content:space-evenly;align-items:center}#cover-letter #create-stuff img,#cover-letter #gazeus img,#cover-letter #solo img,#cover-letter #ustwo img{width:100px;height:86px}#cover-letter #create-stuff h1,#cover-letter #gazeus h1,#cover-letter #solo h1,#cover-letter #ustwo h1{margin:0 50px}
body{font-family:"Mulish",sans-serif !important}.game-section h1{font-size:2em;font-weight:700;margin:30px 0 0 0}.game-section p{font-style:italic}.game-section .paper-content{padding:15px}.game-section .aspect-ratio{position:relative;padding-bottom:56.25%;margin:auto}.game-section .aspect-ratio iframe{border-radius:10px;position:absolute;width:100%;height:100%;left:0;top:0}.game-section img.responsive{width:100%}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

