#cover-letter {
    #me {
        text-align: center;

        img {
            width: 100%;
            max-width: 400px;
            border-radius: 400px;
            border: 10px solid #fff;
            box-shadow: 0 0 10px #aaa;
            margin: auto;
        }
    }

    p {
        text-align: justify;

        &.lead {
            font-size: 1.3rem;
            text-align: center;
            margin-bottom: 20px;
        }
    }

    .session-divider {
        text-align: center;
        font-size: 40px;
        color: #aaa;
        margin-bottom: 30px;
        width: 100%;
    }

    iframe {
        width: 100%;
    }

    #create-stuff,
    #gazeus,
    #solo,
    #ustwo {
        display: flex;
        justify-content: space-evenly;
        align-items: center;

        img {
            width: 100px;
            height: 86px;
        }

        h1 {
            margin: 0 50px;
        }
    }
}
